.wrapper_menu_header {
  width: 1110px;
  height: 490px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 32px;

  .banner {
    width: 540px;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: url('../../../assets/banner.jpg') no-repeat center;

    .wrapper_text {
      width: 289px;
      height: 100px;
      background-color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      h5 {
        text-align: center;
        height: 13px;
        font-family: Spartan;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 13px;
        letter-spacing: 0.06em;
        color: #121212;
        opacity: 0.4;
      }
  
      h3 {
        margin-top: 13px;
        font-family: Spartan;
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 25px;
        display: flex;
        align-items: center;
        letter-spacing: 0.06em;
        color: #121212;
      }

    }


    .slide_left {
      position: absolute;
      width: 40px;
      height: 40px;
      background: url("../../../assets/arrow-left.png") center no-repeat;
      left: 24px;
      top: 45%;
      cursor: pointer;
      background-color: #fff;
      border-radius: 50px;
    }

    .slide_right {
      position: absolute;
      width: 40px;
      height: 40px;
      background: url("../../../assets/arrow-right.png") center no-repeat;
      left: calc(100% - 64px);
      top: 45%;
      cursor: pointer;
      background-color: #fff;
      border-radius: 50px;
    }
  }

  .range {
    width: 540px;
    height: 100%;
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    .sex {
      width: 540px;
      height: 200px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .women {
        width: 255px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: url("../../../assets/Rectangle.png") center no-repeat;

        .women_btn {
          width: 118px;
          height: 48px;
          background-color: white;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;

          p {
            font-family: Spartan;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 22px;
            display: flex;
            align-items: center;
            text-align: center;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            color: #121212;
          }
        }
      }

      .men {
        width: 255px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: url('../../../assets/men_banner.png');

        .men__btn {
          width: 118px;
          height: 48px;
          background-color: white;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;

          p {
            font-family: Spartan;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 22px;
            display: flex;
            align-items: center;
            text-align: center;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            color: #121212;
          }
        }
      }
    }
    .accessories {
      width: 540px;
      height: 260px;
      margin-top: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: url('../../../assets/bag_banner.jpg');

      .accessories_btn {
        width: 166px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;

        p {
          font-family: Spartan;
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 22px;
          display: flex;
          align-items: center;
          letter-spacing: 0.06em;
          text-transform: uppercase;
          color: #121212;
          cursor: pointer;
        }
      }

    }
  }
}
