.wrapper {
  width: 100%;
  height: 580px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  .first_img {
    position: absolute;
    width: 350px;
    height: 276px;
    z-index: -1;
    left: 10px;
    top: 230px;
    background: url('../../../assets/blogfirstimg.jpg');
  }

  .second_img {
    position: absolute;
    width: 350px;
    height: 276px;
    z-index: -1;
    left: 381px;
    top: 230px;
    background: url('../../../assets/blogsecondimg.jpg');
  }

  .third_img {
    position: absolute;
    width: 350px;
    height: 276px;
    z-index: -1;
    left: 751px;
    top: 230px;
    background: url('../../../assets/blogthirdimg.jpg');
  }

  .header {
    width: 100%;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 96px;

    .latest {
      width: 262px;
      height: 25px;
      background: url("../../../assets/LATEST\ FROM\ BLOG.png") no-repeat center;
      margin-left: 35px;
    }

    .see_all {
      width: 56px;
      height: 13px;
      background: url("../../../assets/SEE_ALL.png") no-repeat center;
      margin-right: 35px;
      cursor: pointer;
    }
  }

  .footer {
    width: 100%;
    height: 98px;
    margin-top: 292px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    h5 {
      font-family: Spartan;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #121212;
      text-align: center;
    }

    p {
      margin-top: 12px;
      font-family: Spartan;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 22px;
      color: #121212;
      opacity: 0.8;
    }

    .first_blog {
      width: 270px;
      height: 100%;
      background-color: #fff;
      margin-left: 48px;
    }

    .second_blog {
      width: 270px;
      height: 100%;
      background-color: #fff;

    }

    .third_blog {
      width: 270px;
      height: 100%;
      background-color: #fff;
      margin-right: 49px;
    }
  }
}
